.palette_con {
    display: flex;
    width: 100%;
    height: 100%;

    .palette_left {
        width: 300px;
        min-width: 150px;
        height: 100%;
        background-color: rgba(0, 255, 255,0.2);
    }

    .palette_right {
        flex: 1;
        .palette_disposition {
            width: 100%;
            height: 200px;
            background-color: rgb(255, 228, 196,0.1);
        }
        .palette_canvas {
            height: calc(100% - 200px);
            box-sizing: border-box;
            padding: 10px;
            margin-bottom: 5px;
        }
    }
}